.container {
    display: flex;
    justify-content: center;
  
    width: 320px;
    margin: 0 auto;
  }
  
  @media screen and (min-width: 768px) {
    .container {
      width: 768px;
    }
  }
  
  @media screen and (min-width: 1440px) {
    .container {
      width: 1440px;
    }
  }
  
  .wrapper {
    width: 300px;
    padding: 50px;
    box-shadow: var(--main-shadow);
  }
  
  @media screen and (min-width: 768px) {
    .wrapper {
      width: 500px;
    }
  }